<template>
  <div>
    <div class="widgetContainer widgetContainer--center widgetContainer--scrollable bg-white ownersList">
      <div class="navigationHeader">
        <div class="navigationHeader__inner">
          <span
            class="icon-Arrow-big-left cancel"
            @click="goToPreviousScreen" />
          <p class="title">
            {{ $t('ownerList_NavTitle') }}
          </p>
        </div>
      </div>

      <div class="widgetContainer__body">
        <el-form>
          <div class="wise-form">
            <p class="wise-form__header">
              {{ $t('ownerList_HeaderTitle') }}
            </p>
            <div
              class="ownersList__greater-than"
              v-if="owners && owners.length">
              <ul>
                <li v-for="o in owners" :key="o.id">
                  <div class="details">
                    <p class="label">
                      {{ o.person.firstName }} {{ o.person.lastName }}
                    </p>
                    <span class="value">{{ o.ownership }}%</span>
                  </div>
                  <div>
                    <el-form-item
                      prop="isControlPerson"
                      class="is-no-asterisk column-input">
                      <el-switch @change="controlPersonChange(o)" v-model="o.isControlPerson" active-text="Is Control Person?" />
                    </el-form-item>

                    <el-form-item
                      prop="designation"
                      class="column-input"
                      :label="$t('ownerList_designation')">
                      <el-select
                        v-if="o.isControlPerson"
                        v-model="o.designation"
                        class="is-asterisk">
                        <el-option v-for="designation in controlPersonDesignations" :key="designation.value" :label="designation.label" :value="designation.value" />
                      </el-select>

                      <el-select
                        v-else
                        v-model="o.designation"
                        class="is-asterisk">
                        <el-option v-for="designation in designations" :key="designation.value" :label="designation.label" :value="designation.value" />
                      </el-select>
                    </el-form-item>

                    <el-form-item class="is-no-asterisk column-input" :label="$t('ownerList_designationTitle')" :rules="{required: true}" v-if="o.designation === 'others'">
                      <el-input v-model="o.designationOther" />
                    </el-form-item>
                  </div>
                </li>
              </ul>
            </div>

            <div v-if="totalOwnerShip <= 75">
              <p class="wise-form__header">
                {{ $t('ownerList_AddNew_HeaderTitle') }}
              </p>
              <div class="listSimpleArrows no-status" @click="addOwner">
                <li>
                  <p>{{ $t('ownerList_AddNew_SubTitle') }}</p>
                  <span class="icon-Arrow-small-right" />
                </li>
              </div>
            </div>

            <p class="ownersList__verify-description wise-form__info">
              {{ $t('ownerList_Desc') }}
            </p>
          </div>
          <div class="widgetContainer__footer--fixed">
            <el-button :disabled="!isValid" type="primary" @click="proceedTosign" class="el-button__brand brand">
              {{ $t('ownerList_NextButton') }}
            </el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapMutations, mapGetters } from 'vuex';
import businessMixin from '../../mixins/business';
export default {
  name: 'OwnersList',
  mixins: [businessMixin],
  data() {
    return {
      owners: [],
    };
  },
  computed: {
    ...mapGetters('account', ['getSelectedAccountType']),
    totalOwnerShip() {
      let total = 0;
      this.owners.forEach((o) => {
        total += parseInt(o.ownership);
      });
      return total;
    },
    designations() {
      return  [
        { value: 'chiefExecutiveOfficer', label: 'Chief Executive Officer' },
        { value: 'chiefFinancialOfficer', label: 'Chief Financial Officer' },
        { value: 'chiefOperatingOfficer', label: 'Chief Operating Officer' },
        { value: 'chiefLegalOfficer', label: 'Chief Legal Officer' },
        { value: 'generalCounsel', label: 'General Counsel' },
        { value: 'managingMember', label: 'Managing Member' },
        { value: 'member', label: 'Member' },
        { value: 'generalPartner', label: 'General Partner' },
        { value: 'managingPartner', label: 'Managing Partner' },
        { value: 'partner', label: 'Partner' },
        { value: 'president', label: 'President' },
        { value: 'executiveVicePresident', label: 'Executive Vice President' },
        { value: 'vicePresident', label: 'Vice President' },
        { value: 'treasurer', label: 'Treasurer' },
        { value: 'comptroller', label: 'Comptroller' },
        { value: 'managingDirector', label: 'Managing Director' },
        { value: 'chairmanOfTheBoardOfDirectors', label: 'Chairman of the Board of Directors' },
        { value: 'chiefRevenueOfficer', label: 'Chief Revenue Officer' },
        { value: 'financialAdvisor', label: 'Financial Advisor' },
        { value: 'designer', label: 'Designer' },
        { value: 'chiefTechnologyOfficer', label: 'Chief Technology Officer' },
        { value: 'engineer', label: 'Engineer' },
        { value: 'analyst', label: 'Analyst' },
        { value: 'controller', label: 'Controller' },
        { value: 'manager', label: 'Manager' },
        { value: 'financeManager', label: 'Finance Manager' },
        { value: 'salesManager', label: 'Sales Manager' },
        { value: 'chiefCreditOfficer', label: 'Chief Credit Officer' },
        { value: 'accountManager', label: 'Account Manager' },
        { value: 'chairman', label: 'Chairman' },
        { value: 'generalManager', label: 'General Manager' },
        { value: 'chiefMarketingOfficer', label: 'Chief Marketing Officer' },
        { value: 'headOfOperations', label: 'Head of Operations' },
        { value: 'chiefOfStaff', label: 'Chief of Staff' },
        { value: 'taxSpecialist', label: 'Tax Specialist' },
        { value: 'founder', label: 'Founder' },
        { value: 'chiefProductOfficer', label: 'Chief Product Officer' },
        { value: 'projectManager', label: 'Project Manager' },
        { value: 'accountant', label: 'Accountant' },
        { value: 'secretary', label: 'Secretary' },
        { value: 'digitalMarketingManager', label: 'Digital Marketing Manager' },
        { value: 'chiefBusinessOfficer', label: 'Chief Business Officer' },
        { value: 'chiefInnovationOfficer', label: 'Chief Innovation Officer' },
        { value: 'headOfFinance', label: 'Head of Finance' },
        { value: 'chiefBrandOfficer', label: 'Chief Brand Officer' },
        { value: 'humanResourcesManager', label: 'Human Resources Manager' },
        { value: 'productManager', label: 'Product Manager' },
        { value: 'others', label: 'Others' }
      ];
    },
    controlPersonDesignations() {
      return [{ value: 'chiefExecutiveOfficer', label: 'Chief Executive Officer' },
        { value: 'chiefFinancialOfficer', label: 'Chief Financial Officer' },
        { value: 'chiefOperatingOfficer', label: 'Chief Operating Officer' },
        { value: 'chiefLegalOfficer', label: 'Chief Legal Officer' },
        { value: 'generalCounsel', label: 'General Counsel' },
        { value: 'managingMember', label: 'Managing Member' },
        { value: 'member', label: 'Member' },
        { value: 'generalPartner', label: 'General Partner' },
        { value: 'managingPartner', label: 'Managing Partner' },
        { value: 'partner', label: 'Partner' },
        { value: 'president', label: 'President' },
        { value: 'executiveVicePresident', label: 'Executive Vice President' },
        { value: 'vicePresident', label: 'Vice President' },
        { value: 'treasurer', label: 'Treasurer' },
        { value: 'comptroller', label: 'Comptroller' },
        { value: 'managingDirector', label: 'Managing Director' },
        { value: 'chairmanOfTheBoardOfDirectors', label: 'Chairman of the Board of Directors' }]
    },
    isValid() {
      let isControlPersonSelected = false;
      let designationCheck = true;
      this.owners.forEach((o) => {
        if(o.isControlPerson) {
          isControlPersonSelected = true
        }
        if(o.designation === '') {
          designationCheck = false
        }
      })
      return isControlPersonSelected && designationCheck
    }
  },
  methods: {
    ...mapActions('kyb', ['fetchAllOwners', 'updateOwner']),
    ...mapMutations('kyb', ['setCreatePageOwnerDetails']),
    ...mapMutations('account', ['updateCreateAccountData']),
    afterBusiness() {
      this.getAllOwners(this.getSelectedBusiness.id);
    },
    getAllOwners(businessId) {
      const loader = this.showLoader();
      this.fetchAllOwners(businessId)
        .then((res) => {
          if (res.data.length !== 0) {
            let createdPerson = null;
            let controlPerson = null;
            res.data.forEach((o) => {
              if(o.person.id === this.getSelectedBusiness.createdPersonId) {
                createdPerson = o;
              }
              if(o.isControlPerson) {
                controlPerson = o;
              }
            })
            if(!controlPerson && createdPerson) {
              createdPerson.isControlPerson = true;
            }
            /** @type {import('@m/kyb/store/state').kybState['owners']}*/
            this.owners = res.data;
          }
        })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          loader.close();
        });
    },
    addOwner() {
      this.setCreatePageOwnerDetails(null);
      this.$router.push('/kyb/ownerDetails');
    },
    proceedTosign() {
      if(this.isValid) {
        const promises = [];

        this.owners.forEach((o) => {
          let designation = o.designation
          if(designation === 'others') {
            designation = o.designationOther;
          }
          promises.push(this.updateOwner({ownerId: o.id, details: {
            isControlPerson: o.isControlPerson,
            designation: designation
          }}))
        })
        const loader = this.showLoader();
        Promise.all(promises)
          .then(() => {
            setTimeout(() => {
              this.$router.push('/kyb/eSign')
            }, 0)
          })
          .finally(() => {
            loader.close();
          });
      }
    },
    proceedToAccountCreation() {
      this.updateCreateAccountData({
        businessId: this.getSelectedBusiness.id,
        type: 'businessChecking'
      });
      
      this.$router.push('/account/setup');
      
    },
    controlPersonChange(owner) {
      owner.designation = '';
      this.owners.forEach((o) => {
        if(o.id !== owner.id) {
          o.isControlPerson = false;
          o.designation = '';
        }
      })
    }
  }
};
</script>

